.details-restaurant {
  @include section(24, 40);
  background-color: rgba(0, 0, 0, 0.65);
  background-image: linear-gradient(
      to right,
      var(--hero-gradient),
      var(--hero-gradient)
    ),
    url('../images/details-restaurant/details-restaurant-bg-img_m.jpg');
  background-position: center center; // x y
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
}

.details-restaurant__wrap {
  text-align: start;
  max-width: 250px;
  margin-left: auto;
}

.details-restaurant__logo {
  @extend .logo;
  @extend .logo--white;
  margin-bottom: 12px;
}

.details-restaurant__title {
  @extend %section-title;
  margin-bottom: 20px;
}

.details-restaurant__descr {
  @extend %descr;
  text-align: start;
  color: var(--descr-text-cl);
  max-width: 250px;
  margin-bottom: 48px;
}

.details-rest {
  &.slider-right {
    margin-right: -15px;
    margin-bottom: 32px;
    margin-left: 25px;
    max-width: 374px;
    height: 322px;
  }

  & .slick-track {
    height: 270px;
    margin-bottom: 16px;
  }

  & .slider-item {
    width: 274px;
    height: 270px;
    overflow: hidden;
    margin-right: 25px;
  }

  & .slick-arrow {
    bottom: 0;
    left: 0;
  }

  & .slick-arrow.slick-prev {
    right: 81;
    background-image: url(./../images/svg/arrow-left-white.svg);

    &:hover {
      background-image: url(./../images/svg/arrow-left-hovered.svg);
    }
  }

  & .slick-arrow.slick-next {
    left: 64;
    background-image: url(./../images/svg/arrow-right-white.svg);

    &:hover {
      background-image: url(./../images/svg/arrow-right-hovered.svg);
    }
  }
}

.details-restaurant__btn-wrap > .btn:first-child {
  margin-bottom: 24px;
}
